.spotlight-banner.data-selector--wrapper {
  padding: 1em 2em 3rem 2em;
}

.spotlight-banner .labelled-data-selector--wrapper .data-selector {
  left: 0;
}

.labelled-data-selector--wrapper {
  display: inline-block;
}

.charts__chart .table-styled table {
  width: 100%;
}

.auto-height {
  min-height: auto !important;
}

.custom-legend {
  display: flex;
  justify-content: center;
  flex-flow: row wrap;
  min-height: 160px;
}

.custom-legend.right {
  display: block;
  width: 350px;
  position: absolute;
  right: 0;
  z-index: 1;
  top: 100px;
}

@media (max-width: 1350px) {
  .custom-legend.right {
    position: relative;
    width: 100%;
    top: auto;
  }
}

@media screen and (max-width: 412px) {
  .custom-legend.right {
      display: block;
  }
}

.legend-item {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin-right: 5px;
}

.right .legend-item {
  margin-bottom: 5px;
}

.badge {
  display: inline-block;
  padding: 0.25em 0.4em;
  font-size: 75%;
  font-weight: 700;
  line-height: 1;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  border-radius: 0.25rem;
  width: 20px;
  height: 20px;
  padding: 0.7rem;
}

.legend-item .text {
  vertical-align: top;
  margin-left: 5px;
  font-size: 12px;
}

@media screen and (max-width: 699px) {
  .chart-container .charts__chart {
      display: block;
  }
}

.legend-item.label {
  border-bottom: 2px solid;
}

.label .text {
  font-size: 13px;
}

.no-data--wrapper {
  display: table;
  position: absolute;
  width: 100%;
  min-height: 450px;
}

.no-data {
  font-size: 24px;
  text-align: center;
  vertical-align: middle;
  display: table-cell;
}

.display-none {
  display: none;
}

.invisible {
  visibility: hidden;
}
